/* CarouselCard.css */

.central-card {
    position: relative;
    width: 100%;
    height: 100%;
    padding-top: px;
    box-sizing: border-box;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius: 25px;
    box-shadow: 0px 9px 9px 0px #1365E233;
  }

.card-container-left,
.card-container-right {
  position: relative;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 25px;
}


.card-image {
  width: 100%;
  height: 100%;
  object-fit: fill;
  border-radius: 25px;

}



.title-frame {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30px;
  background-color: black;
  opacity: 0.5;
  border-radius: 0 0 25px 25px;
}

.card-title {
  font-size: 16px;
  font-weight: bold;
  color: white;
  margin: 0;
  padding: 5px;
  text-align: center; 

}
